.my-calendar {
  display: flex;
  position: relative;
  height: 100%;
  width: 100%;
  flex-direction: column;
  justify-content: space-between;
}
.my-calendar .calendar-component {
  display: flex;
  width: 100%;
  height: calc(100% - 40px);
  flex-direction: column;
  overflow-y: auto;
}
.my-calendar .appointment-history-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  z-index: 25;
  background-color: #f2f3f3;
}
