.m .articles {
  display: flex;
  flex-direction: column;
  position: relative;
  top: 64px;
  height: calc(100vh - 64px);
  width: 100%;
  justify-content: space-between;
  background-color: rgba(212, 215, 217, 0.1);
}
.m .articles .loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}
.m .articles .articles-content {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  overflow-y: auto;
  padding: 16px 8px 88px 16px;
}
.m .articles .articles-content .ant-tabs-nav-container-scrolling {
  padding: 0;
}
.m .articles .articles-content .ant-tabs-tab-next,
.m .articles .articles-content .ant-tabs-tab-prev {
  display: none;
}
.m .articles .articles-content .like,
.m .articles .articles-content .share {
  height: 18px;
  width: 18px;
}
.m .articles .articles-content .article-title {
  border-bottom: solid 1px #d4d7d9;
}
.m .articles .articles-content .sortBy {
  height: 40px;
  background: rgba(212, 215, 217, 0.3);
  border-radius: 4px;
}
.m .articles .articles-content .sortBy .ant-select .ant-select-selection {
  height: 40px;
  width: 100%;
  border: 0;
  background: rgba(212, 215, 217, 0);
  margin-bottom: 12px;
}
.m .articles .articles-content .sortBy .ant-select-focused .ant-select-selection {
  box-shadow: none;
  border: 0;
}
.m .articles .articles-content .sortBy .ant-select-open .ant-select-selection {
  box-shadow: none;
  border: 0;
}
.m .articles .articles-content .sortBy .ant-select .ant-select-selection .ant-select-selection__rendered .ant-select-selection-selected-value {
  color: #2b3a42;
  font-family: "AvenirNext-Medium";
}
.m .articles .articles-content .sortBy .ant-select .ant-select-selection .ant-select-selection__rendered {
  margin: 0;
  padding: 5px 10px;
}
.m .articles .articles-footer {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  height: 72px;
  padding: 16px;
  border: solid 1px #d4d7d9;
}
.m .articles .articles-footer .arrow-wrapper {
  font-size: 24px;
  margin-right: 16px;
}
.m .articles .articles-footer .text {
  font-size: 12px;
  color: #2b3a42;
}
