.m .summary {
  display: flex;
  flex-direction: column;
  position: relative;
  top: 64px;
  height: calc(100vh - 64px);
  width: 100%;
  justify-content: space-between;
  background-color: rgba(212, 215, 217, 0.1);
}
.m .summary .loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}
.m .summary .summary-content {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  overflow-y: auto;
  padding: 16px;
}
.m .summary .summary-content .program-info {
  margin-top: 8px;
  font-size: 16px;
  font-weight: 500;
}
.m .summary .summary-content .product {
  width: 115px;
  height: 32px;
  border-radius: 20px;
  background-color: rgba(204, 237, 243, 0.5);
  margin-right: 8px;
  margin-bottom: 8px;
}
.m .summary .summary-content .product span:nth-child(1) {
  margin-right: 12px;
}
.m .summary .summary-content .product span:nth-child(2) {
  text-align: center;
  font-size: 12px;
  color: #005587;
  font-family: "AvenirNext-Medium";
}
.m .summary .summary-footer {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  height: 72px;
  padding: 16px;
  border: solid 1px #d4d7d9;
}
.m .summary .summary-footer .arrow-wrapper {
  font-size: 24px;
  margin-right: 16px;
}
.m .summary .summary-footer .text {
  font-size: 12px;
  color: #2b3a42;
}
