.m .home .footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 40px;
  z-index: 25;
  background: #ffffff;
  border: 1px solid #d4d7d9;
  z-index: 10;
}
.m .home .footer .ant-menu {
  display: flex;
  justify-content: space-between;
  height: 100%;
}
.m .home .footer .ant-menu .ant-menu-item {
  color: #aab0b3;
  font-family: "AvenirNext-Medium";
  display: flex;
  align-items: center;
  padding: 1px 10px;
}
.m .home .footer .ant-menu .ant-menu-item > a {
  color: #aab0b3;
}
.m .home .footer .ant-menu .ant-menu-item-active {
  border-bottom: 4px solid #2b3a42;
  color: #2b3a42;
}
.m .home .footer .ant-menu .ant-menu-item-active > a {
  color: #2b3a42;
}
.m .home .footer .ant-menu .ant-menu-item-selected {
  border-bottom: 4px solid #2b3a42 !important;
  color: #2b3a42;
}
.m .home .footer .ant-menu .ant-menu-item-selected > a {
  color: #2b3a42;
}
.m .hide-scroll {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.m .hide-scroll::-webkit-scrollbar {
  display: none;
}
.m .loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}
.m .menu-black .ant-menu {
  display: flex;
  justify-content: space-between;
  height: 100%;
}
.m .menu-black .ant-menu .ant-menu-item {
  color: #aab0b3;
  font-family: "AvenirNext-Medium";
  display: flex;
  align-items: center;
  width: 50%;
  justify-content: center;
  padding: 1px 10px;
}
.m .menu-black .ant-menu .ant-menu-item > a {
  color: #aab0b3;
}
.m .menu-black .ant-menu .ant-menu-item-active {
  border-bottom: 4px solid #2b3a42;
  color: #2b3a42;
}
.m .menu-black .ant-menu .ant-menu-item-active > a {
  color: #2b3a42;
}
.m .menu-black .ant-menu .ant-menu-item-selected {
  border-bottom: 4px solid #2b3a42 !important;
  color: #2b3a42;
}
.m .menu-black .ant-menu .ant-menu-item-selected > a {
  color: #2b3a42;
}
