.m .history-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 100vh;
  width: 100%;
}
.m .history-wrapper .history-container {
  margin-top: 64px;
  display: flex;
  flex-direction: column;
  height: calc(100% - 72px);
  width: 100%;
  overflow-y: auto;
}
.m .history-wrapper .back-footer {
  display: flex;
  flex-direction: row;
  padding: 16px;
  height: 72px;
  width: 100%;
  justify-content: flex-start;
  align-items: center;
  z-index: 25;
  border: solid 1px #d4d7d9;
  background-color: #ffffff;
}
.m .history-wrapper .back-footer .arrow-wrapper {
  font-size: 24px;
  margin-right: 16px;
}
.m .history-wrapper .back-footer .text {
  font-size: 12px;
  color: #2b3a42;
}
