.m .calendar-full {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: auto;
  padding-top: 16px;
}
.m .calendar-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  height: auto;
  padding-top: 12px;
  padding-left: 12px;
  padding-right: 12px;
}
.m .calendar-header .calendar-header-top {
  display: flex;
  width: 100%;
  height: auto;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 8px;
}
.m .calendar-header .calendar-header-top .left .ant-btn.sync-btn {
  height: 32px;
  border: none;
  border-radius: 2px;
  background-color: rgba(212, 215, 217, 0.3);
}
.m .calendar-header .calendar-header-top .left .ant-btn.sync-btn > span {
  color: #2b3a42;
  font-family: "AvenirNext-Medium", serif;
  font-weight: 500;
  font-size: 14px;
}
.m .calendar-header .calendar-header-top .right {
  display: flex;
  align-items: center;
}
.m .calendar-header .calendar-header-bottom {
  display: flex;
  height: auto;
  width: 100%;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin-top: 8px;
}
.m .calendar-header .calendar-header-bottom .show {
  color: #7f888d;
  margin-right: 16px;
}
.m .month-dropdown {
  width: 125px !important;
  height: 32px !important;
  font-family: "AvenirNext-Medium", serif;
}
.m .month-dropdown .ant-select-selection.ant-select-selection--single {
  font-weight: 600;
  width: 125px !important;
  height: 32px;
  border-radius: 2px;
  background-color: rgba(212, 215, 217, 0.3);
  border-color: rgba(212, 215, 217, 0.3);
  line-height: 30px !important;
  color: #2b3a42 !important;
}
.m .month-dropdown .ant-select-selection.ant-select-selection--single .ant-select-selection__rendered {
  height: 32px !important;
}
.m .month-dropdown .ant-select-selection.ant-select-selection--single .ant-select-selection__rendered .ant-select-selection-selected-value {
  height: 32px !important;
  line-height: 32px;
  padding-right: 0;
  padding-left: 0;
  font-weight: 500;
  font-size: 16px;
  color: #2b3a42;
}
.m .month-dropdown .ant-select-selection.ant-select-selection--single .ant-select-arrow {
  top: 30% !important;
}
.m .calendar-row {
  margin: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
  height: 40px;
  text-transform: uppercase;
  padding: 0 12px;
  font-size: 14px;
  font-weight: 500;
  font-style: normal;
  color: #7f888d;
}
.m .calendar-row.border-bottom {
  border-bottom: 1px solid #eee;
}
.m .calendar-row.white {
  background-color: #ffffff;
}
.m .calendar-row.tall {
  height: 48px;
}
.m .calendar-row .days {
  display: flex;
  justify-content: center;
  flex-grow: 1;
  text-align: center;
}
.m .calendar-row .cell {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  height: 100%;
  width: 36px;
  text-align: center;
}
.m .calendar-row .cell .digits {
  font-size: 14px;
  color: #2b3a42;
}
.m .calendar-row .cell .selected-digit {
  height: 24px;
  width: 24px;
  background-color: #005587;
  color: #ffffff;
  border-radius: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.m .calendar-row .cell .disable-date {
  color: #8fa3aa;
}
.m .calendar-row .cell .cell-dots-wrapper {
  display: flex;
  width: 90%;
  align-items: center;
  justify-content: center;
  height: 6px;
  margin-top: 2px;
}
.m .mobile-card-wrapper {
  display: flex;
  height: auto;
  flex-direction: row;
  overflow-x: auto;
  padding-left: 16px;
  margin-bottom: 10px;
}
.m .date-and-line {
  height: 48px;
  font-size: 14px;
  padding: 16px 4px 8px;
  margin-left: 12px;
  margin-right: 12px;
  border-top: 1px solid #eee;
  background-color: #ffffff;
  color: #7f888d;
}
.m .background-gray {
  background: rgba(212, 215, 217, 0.1);
}
.m .mobile-dot {
  height: 6px;
  width: 6px;
  border-radius: 50%;
  display: inline-block;
}
.m .mobile-dot.silver {
  background-color: #d4d7d9;
  border-color: #d4d7d9;
}
.m .mobile-dot.dark {
  background-color: #2b3a42;
  border-color: #2b3a42;
}
.m .mobile-dot.orange {
  background-color: #fe8a12;
  border-color: #fe8a12;
}
.m .mobile-dot.steel-grey {
  background-color: #7f888d;
  border-color: #7f888d;
}
.m .mobile-dot.invisble {
  display: none;
}
