.m .program-details-wrapper {
  display: flex;
  flex-direction: column;
  position: relative;
  top: 64px;
  height: calc(100vh - 64px);
  width: 100%;
  justify-content: space-between;
  background-color: rgba(212, 215, 217, 0.1);
}
.m .program-details-wrapper .program-details-body {
  display: flex;
  flex-direction: column;
  height: auto;
  width: 100%;
  overflow-y: auto;
}
.m .program-details-wrapper .program-details-body .search-section {
  display: flex;
  flex-direction: column;
  height: auto;
  margin: 24px 16px 6px 16px;
}
.m .program-details-wrapper .program-details-body .search-section .search-bar {
  margin-bottom: 6px;
}
.m .program-details-wrapper .program-details-body .search-section .search-bar .ant-input {
  height: 40px;
}
.m .program-details-wrapper .program-details-body .search-section .sort-bar {
  margin-top: 6px;
  height: 40px;
}
.m .program-details-wrapper .program-details-body .search-section .sort-bar .ant-select {
  width: 100%;
}
.m .program-details-wrapper .program-details-body .search-section .sort-bar .ant-select.ant-select-enabled {
  height: 40px !important;
}
.m .program-details-wrapper .program-details-body .search-section .sort-bar .ant-select.ant-select-enabled .ant-select-selection {
  background-color: rgba(212, 215, 217, 0.3) !important;
  border: 0 !important;
  box-shadow: none;
}
.m .program-details-wrapper .program-details-body .search-section .sort-bar .ant-select.ant-select-enabled .ant-select-selection.ant-select-selection--single {
  height: 40px !important;
}
.m .program-details-wrapper .program-details-body .search-section .sort-bar .ant-select.ant-select-enabled .ant-select-selection.ant-select-selection--single .ant-select-selection__rendered .ant-select-selection-selected-value {
  font-family: "AvenirNext-Medium" !important;
  font-size: 14px !important;
}
.m .program-details-wrapper .program-details-body .search-section .sort-bar .ant-select.ant-select-enabled .ant-select-selection.ant-select-selection--single .ant-select-arrow {
  top: 14px;
}
.m .program-details-wrapper .program-details-body .tab-title {
  display: flex;
  flex-direction: column;
  height: auto;
  flex-wrap: wrap;
  margin: 0;
}
.m .program-details-wrapper .program-details-body .tab-title .ant-tabs-bar {
  width: 100%;
}
.m .program-details-wrapper .program-details-body .tab-title .ant-tabs-bar.ant-tabs-top-bar {
  margin-bottom: 16px;
}
.m .program-details-wrapper .program-details-body .tab-title .ant-tabs-bar.ant-tabs-top-bar .ant-tabs-tab-active.ant-tabs-tab {
  flex-grow: 1;
  border-bottom: 4px solid #2b3a42;
  color: #2b3a42;
}
.m .program-details-wrapper .program-details-body .tab-title .ant-tabs-bar.ant-tabs-top-bar .ant-tabs-tab-active.ant-tabs-tab ::selection {
  background: #2b3a42;
}
.m .program-details-wrapper .program-details-body .tab-title .ant-tabs-bar.ant-tabs-top-bar .ant-tabs-ink-bar {
  background-color: #2b3a42;
}
.m .program-details-wrapper .program-details-body .tab-title .ant-tabs-nav.ant-tabs-tabpane-active.tabscontent {
  flex-wrap: wrap;
}
.m .program-details-wrapper .program-details-body .program-details-lower-body {
  display: flex;
  margin-top: 6px;
  padding: 0 16px 16px 16px;
}
.m .program-details-wrapper .program-details-footer {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.m .program-details-wrapper .program-details-footer .view-program-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  z-index: 25;
  background-color: #f2f3f3;
}
.m .program-details-wrapper .program-details-footer .menu-selection {
  height: 40px;
  width: 100%;
  z-index: 25;
  background-color: #d4d7d9;
}
.m .program-details-wrapper .program-details-footer .nav-bar {
  display: flex;
  flex-direction: row;
  padding: 16px;
  height: 72px;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  z-index: 25;
  border: solid 1px #d4d7d9;
  background-color: #ffffff;
}
.m .program-details-wrapper .program-details-footer .nav-bar .nav-bar-left {
  display: flex;
  flex-direction: row;
  width: 110px;
  align-items: center;
  margin-right: 16px;
}
.m .program-details-wrapper .program-details-footer .nav-bar .nav-bar-left .arrow-wrapper {
  font-size: 24px;
  margin-right: 16px;
}
.m .program-details-wrapper .program-details-footer .nav-bar .nav-bar-left .text {
  font-size: 12px;
  color: #2b3a42;
}
.m .program-details-wrapper .program-details-footer .nav-bar .nav-bar-right {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  min-width: 70px;
}
.m .program-details-wrapper .program-details-footer .nav-bar .nav-bar-right .program-name {
  font-size: 18px;
  margin-right: 4px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.m .program-details-wrapper .program-details-footer .nav-bar .nav-bar-right .bubble {
  margin-left: 4px;
  font-size: 12px;
}
.m .program-details-wrapper .program-details-footer .nav-bar .nav-bar-right .bubble.active {
  background-color: #e3f3df;
  width: 50px;
  height: 24px;
  border-radius: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.m .discharge-bar {
  display: flex;
  justify-content: space-between;
  height: 72px;
  padding: 16px;
  width: 100%;
  align-items: center;
}
.m .discharge-bar .discharge-text {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.m .discharge-bar .discharge-text .image {
  margin-right: 6px;
}
.m .discharge-bar .discharge-text .text {
  font-size: 12px;
  margin-left: 6px;
}
.m .discharge-bar .warning {
  color: #ff530d;
  background-color: #ffffff;
  border-color: #ff530d;
}
.m .program-success-msg {
  top: 136px;
  bottom: 0;
}
.m .listBody {
  width: calc(100vw - 377px);
  height: calc(100vh - calc(64px + 72px));
}
