.m .appointment-header {
  width: 100%;
  height: auto;
  margin-top: 24px;
  margin-bottom: 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 16px;
  padding-right: 16px;
}
.m .appointment-header .header-title {
  font-family: "AvenirNext-DemiBold";
  color: #2b3a42;
  font-size: 22px;
  font-weight: 600;
  height: 30px;
}
.m .header-sub-title {
  font-family: "AvenirNext-DemiBold";
  color: #2b3a42;
  font-size: 14px;
  font-weight: 500;
  text-align: right;
  margin-right: 8px;
  text-decoration: underline;
}
.m .header-sub-title.invisible {
  display: none;
}
.m .dashboard-days {
  height: 16px;
  font-size: 12px;
  line-height: normal;
  width: 90%;
  margin: 0 auto;
  color: #7f888d;
  font-family: "AvenirNext-Regular";
  padding-left: 16px;
  padding-right: 16px;
}
.m .dashboard-row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding-left: 16px;
  padding-right: 16px;
}
.m .date-display {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin-top: 8px;
  color: #7f888d;
  font-stretch: normal;
  padding-left: 16px;
  padding-right: 16px;
}
.m .date-display .today {
  margin-right: 6px;
  display: flex;
  align-items: center;
  width: 50px;
  justify-content: space-between;
}
.m .date-section {
  display: flex;
  flex-direction: inherit;
  margin-top: 8px;
  height: 24px;
  padding-left: 16px;
  padding-right: 16px;
}
.m .date-section .dashboard-dates {
  display: flex;
  flex-direction: row;
  height: 32px;
  align-items: center;
}
.m .date-section .dashboard-dates .dashboard-icons {
  width: 5%;
}
.m .date-section .dashboard-dates .dates-row {
  display: flex;
  width: 90%;
  color: #2b3a42;
  font-family: "AvenirNext-Medium";
  align-items: center;
  justify-content: space-around;
  height: 100%;
}
.m .date-section .dashboard-dates .dates-row .week-dates {
  width: 32px;
  height: 32px;
  align-items: center;
  justify-content: center;
  display: flex;
}
.m .date-section .dashboard-dates .dates-row .week-dates.isSelected {
  background-color: #2b3a42;
  color: white;
  border-radius: 50%;
}
.m .date-section .dashboard-dates .dates-row .week-dates.isFadeSelected {
  background-color: #007a97;
  color: white;
  border-radius: 50%;
  z-index: 1;
}
.m .col {
  flex-grow: 1;
  flex-basis: 0;
  max-width: 100%;
}
.m .col-start {
  justify-content: flex-start;
  text-align: left;
}
.m .col-center {
  justify-content: center;
  text-align: center;
}
.m .mobile-card-wrapper {
  display: flex;
  height: auto;
  flex-direction: row;
  overflow-x: auto;
  padding-left: 16px;
}
.m .pointer {
  cursor: default;
}
.m .dots-wrapper {
  display: flex;
  width: 90%;
  align-items: center;
  justify-content: space-evenly;
  height: auto;
  margin: 0 auto;
  padding-left: 16px;
  padding-right: 16px;
}
.m .dots-wrapper .week-dots {
  width: 100%;
  height: 4px;
  align-items: center;
  justify-content: center;
  display: flex;
}
.m .dots-wrapper .week-dots.isSelected {
  background-color: #2b3a42;
  color: white;
  border-radius: 50%;
}
.m .dots-wrapper .week-dots.isFadeSelected {
  background-color: #0055978f;
  color: white;
  border-radius: 50%;
}
.m .reminder-menu {
  margin-top: 16px;
  height: 40px;
  margin-bottom: 8px;
  padding-left: 16px;
  padding-right: 16px;
}
.m .reminder-menu .ant-menu-horizontal {
  line-height: 36px !important;
}
.m .reminder-menu .ant-menu-item {
  color: #aab0b3;
  font-family: "AvenirNext-Medium";
  padding: 1px 10px;
}
.m .reminder-menu .ant-menu-item > a {
  color: #aab0b3;
}
.m .reminder-menu .ant-menu-item.ant-menu-item-active.ant-menu-item-selected {
  border-bottom: 4px solid #2b3a42 !important;
}
.m .reminder-menu .ant-menu-item.ant-menu-item-active.ant-menu-item-selected:hover {
  border-bottom: 4px solid #2b3a42 !important;
  color: #2b3a42;
}
.m .reminder-menu .ant-menu-item.ant-menu-item-active.ant-menu-item-selected > a {
  color: #2b3a42;
}
.m .reminder-menu .ant-menu-item.ant-menu-item:hover {
  border-bottom: 2px solid #2b3a42 !important;
}
.m .reminder-menu .ant-menu-item-selected {
  border-bottom: 4px solid #2b3a42 !important;
  color: #2b3a42;
}
.m .reminder-menu .ant-menu-item-selected:hover {
  border-bottom: 4px solid #2b3a42 !important;
  color: #2b3a42;
}
.m .reminder-menu .ant-menu-item-selected > a {
  color: #2b3a42;
}
.m .reminder-menu .ant-menu-item-selected > a:hover {
  color: #2b3a42;
}
.m .line {
  height: 1px;
  opacity: 0.5;
  margin-top: 4px;
  background-color: #d4d7d9;
  border: solid 1px #d4d7d9;
  margin-left: 16px;
  margin-right: 16px;
}
.m .survey-body {
  display: flex;
  height: auto;
  overflow-x: scroll;
  width: auto;
  padding: 16px;
}
.m .survey-body .survey-card-mobile {
  width: auto;
  height: auto;
  margin: 0;
  margin-bottom: 16px;
  margin-right: 16px;
  min-width: -webkit-fill-available;
  border: 1px solid #d4d7d9;
}
.m .survey-body .survey-card-mobile .card-content {
  flex-direction: column;
  display: flex;
}
.m .survey-body .survey-card-mobile .card-content .survey-card-header .survey-card-header-right {
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
}
.m .survey-body .survey-card-mobile .card-content .survey-card-header .survey-card-header-right .in-progress-container {
  display: flex;
  width: auto;
  border-radius: 25px;
  padding-left: 10px;
  padding-right: 10px;
  height: 24px;
  font-size: 12px;
  line-height: 2;
}
.m .survey-body .survey-card-mobile .card-content .survey-card-header .survey-card-header-right .in-progress-container.inprogress {
  background-color: #e3f3df;
}
.m .survey-body .survey-card-mobile .card-content .survey-card-header .survey-card-header-right .in-progress-container.completed {
  background-color: #ccedf3;
}
.m .survey-body .survey-card-mobile .card-content .survey-card-header .survey-card-header-right .survey-header-button {
  display: flex;
  width: auto;
  height: fit-content;
}
.m .survey-body .survey-card-mobile .card-content .survey-card-header .survey-card-header-right .survey-header-button .ant-btn {
  width: 147px;
  height: 32px;
  border-radius: 2px;
}
