.m .questionnnaire-content-wrapper {
  display: flex;
  flex-direction: column;
  position: relative;
  top: 64px;
  height: calc(100vh - 64px);
  width: 100%;
  justify-content: space-between;
  background-color: rgba(212, 215, 217, 0.1);
}
.m .questionnnaire-content-wrapper .questionnnaire-content {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  overflow-y: auto;
  padding: 16px;
}
.m .questionnnaire-content-wrapper .questionnnaire-content .newSurvey {
  margin-bottom: 8px;
  width: 45px;
  height: 24px;
  border-radius: 12px;
  background-color: #e3f3df;
  display: flex;
  justify-content: center;
  align-items: center;
}
.m .questionnnaire-content-wrapper .questionnaire-footer {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  height: 72px;
  padding: 16px;
  border: solid 1px #d4d7d9;
}
.m .questionnnaire-content-wrapper .back-location {
  padding: 24px;
}
