.m .article {
  display: flex;
  flex-direction: column;
  position: relative;
  top: 64px;
  height: calc(100vh - 64px);
  width: 100%;
  justify-content: space-between;
  background-color: rgba(212, 215, 217, 0.1);
}
.m .article .loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}
.m .article .article-content {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  overflow-y: auto;
  padding: 16px 16px 88px 16px;
}
.m .article .article-content .like,
.m .article .article-content .share {
  height: 18px;
  width: 18px;
}
.m .article .article-content .article-title {
  border-bottom: solid 1px #d4d7d9;
}
.m .article .article-footer {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  height: 72px;
  padding: 16px;
  border: solid 1px #d4d7d9;
}
.m .article .article-footer .arrow-wrapper {
  font-size: 24px;
  margin-right: 16px;
}
.m .article .article-footer .more,
.m .article .article-footer .usr {
  background-color: #f4f4f4;
  width: 32px;
  height: 32px;
  border-radius: 16px;
}
.m .article .article-footer .text {
  font-size: 12px;
  color: #2b3a42;
}
