.createSurvey {
  position: relative;
  top: 64px;
  display: flex;
  height: calc(100vh - 64px);
  min-height: 100%;
  /* width */
  /* Track */
  /* Handle */
  /* Handle on hover */
}
.createSurvey .MenuOption {
  position: absolute;
  top: 25px;
  left: 42%;
}
.createSurvey .profileHead {
  height: 72px;
  border-bottom: 1px solid #d4d7d9;
  position: fixed;
  bottom: 0;
  width: 100%;
  background: #ffffff;
  z-index: 2;
}
.createSurvey .back-location {
  padding: 24px;
}
.createSurvey .previewQuestionnaire {
  position: fixed;
  bottom: 72px;
  width: 100%;
  z-index: 111;
}
.createSurvey .previewQuestionnaire .ant-btn {
  width: 100%;
  border: 0;
  height: 40px;
  background-color: #f2f3f3;
  border-radius: 2px;
}
.createSurvey .backButton {
  width: 24px;
  height: 24px;
}
.createSurvey .patient-pane {
  height: 100%;
  background-color: rgba(212, 215, 217, 0.1);
}
.createSurvey .patient-pane .patients-selection {
  height: 100%;
  overflow-y: auto;
  padding: 16px;
}
.createSurvey .patient-pane .arrow-img-program-select {
  margin-top: -5px;
}
.createSurvey .patient-pane .survey-detail-input.ant-input-affix-wrapper .ant-input {
  height: 40px;
}
.createSurvey .patient-pane .search-icons {
  margin-right: 12px;
  width: 17px;
  height: 17px;
}
.createSurvey .patient-pane .multiselect {
  width: 19px;
  height: 20px;
}
.createSurvey .patient-pane .survey-detail-select {
  height: 40px;
  flex-grow: 1;
  width: 97%;
  background: rgba(212, 215, 217, 0.3);
  border-radius: 4px;
}
.createSurvey .patient-pane .survey-detail-select .ant-select .ant-select-selection {
  height: 40px;
  border: 0;
  background: rgba(212, 215, 217, 0);
  margin-bottom: 10px;
}
.createSurvey .patient-pane .survey-detail-select .ant-select-focused .ant-select-selection {
  box-shadow: none;
  border: 0;
}
.createSurvey .patient-pane .survey-detail-select .ant-select-open .ant-select-selection {
  box-shadow: none;
  border: 0;
}
.createSurvey .patient-pane .survey-detail-select .ant-select .ant-select-selection .ant-select-selection__rendered .ant-select-selection-selected-value {
  color: #2b3a42;
  font-family: "AvenirNext-Medium";
}
.createSurvey .patient-pane .survey-detail-select .ant-select .ant-select-selection .ant-select-selection__rendered {
  margin: 0;
  padding: 5px 10px;
}
.createSurvey .patient-pane .survey-detail-select .ant-select .ant-select-selection {
  height: 40px;
  display: flex;
  flex-grow: 1;
  justify-content: space-between;
  border: 0;
  background: rgba(212, 215, 217, 0);
  margin-bottom: 10px;
}
.createSurvey .patient-pane .survey-detail-select .ant-select-selection__rendered {
  margin: 0;
  max-width: 100px;
}
.createSurvey .patient-pane .range-picker .ant-calendar-picker {
  max-width: 250px;
}
.createSurvey .patient-pane .patient-cards {
  max-width: 254px;
}
.createSurvey .patient-pane .the-cards {
  height: auto;
  flex-wrap: wrap;
  padding: 24px 0 200px 0;
  align-content: flex-start;
}
.createSurvey ::-webkit-scrollbar {
  width: 5px;
}
.createSurvey ::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}
.createSurvey ::-webkit-scrollbar-thumb {
  background: #7f888d;
  border-radius: 10px;
}
.createSurvey ::-webkit-scrollbar-thumb:hover {
  background: #d4d7d9;
}
.createSurvey textarea.ant-input {
  width: 65%;
}
