  top: 64px;
  display: flex;
  height: calc(100vh - 64px);
  width: 100%;
  min-height: 100%;
  /* width */
  /* Track */
  /* Handle */
  /* Handle on hover */
}
.m .surveyDetail .tabscontent {
  overflow: auto;
}
.m .surveyDetail .MenuOption {
  position: fixed;
  bottom: 72px;
  width: 100%;
  border-top: 1px solid #d4d7d9;
}
.m .surveyDetail .MenuOption .ant-menu-horizontal {
  height: 40px;
}
.m .surveyDetail .MenuOption .ant-menu-item {
  display: flex;
  justify-content: center;
  width: 50%;
  color: #aab0b3;
  font-family: "AvenirNext-Medium";
}
.m .surveyDetail .MenuOption .ant-menu-item > a {
  color: #aab0b3;
  padding-bottom: 6px;
}
.m .surveyDetail .MenuOption .ant-menu-item-active {
  border-bottom: 4px solid #2b3a42;
  color: #2b3a42;
}
.m .surveyDetail .MenuOption .ant-menu-item-active > a {
  color: #2b3a42;
}
.m .surveyDetail .MenuOption .ant-menu-item-selected {
  border-bottom: 4px solid #2b3a42 !important;
  color: #2b3a42;
}
.m .surveyDetail .MenuOption .ant-menu-item-selected > a {
  color: #2b3a42;
}
.m .surveyDetail .menu-options {
  display: flex;
  align-items: flex-end;
  justify-content: center;
}
.m .surveyDetail .profileHead {
  height: 72px;
  border-bottom: 1px solid #d4d7d9;
  position: fixed;
  bottom: 0;
  width: 100%;
  background: #ffffff;
  z-index: 2;
}
.m .surveyDetail .back-location {
  padding: 24px;
}
.m .surveyDetail .previewQuestionnaire {
  position: fixed;
  bottom: 112px;
  width: 100%;
  z-index: 111;
}
.m .surveyDetail .previewQuestionnaire .ant-btn {
  width: 100%;
  border: 0;
  height: 40px;
  background-color: #f2f3f3;
  border-radius: 2px;
}
.m .surveyDetail .backButton {
  width: 24px;
  height: 24px;
}
.m .surveyDetail .patient-pane {
  height: 100%;
  background-color: rgba(212, 215, 217, 0.1);
}
.m .surveyDetail .patient-pane .patients-selection {
  height: 100%;
  overflow-y: auto;
  padding: 16px;
}
.m .surveyDetail .patient-pane .arrow-img-program-select {
  margin-top: -5px;
}
.m .surveyDetail .patient-pane .survey-detail-input.ant-input-affix-wrapper .ant-input {
  height: 40px;
}
.m .surveyDetail .patient-pane .search-icons {
  margin-right: 12px;
  width: 17px;
  height: 17px;
}
.m .surveyDetail .patient-pane .multiselect {
  width: 19px;
  height: 20px;
}
.m .surveyDetail .patient-pane .survey-detail-select {
  height: 40px;
  flex-grow: 1;
  width: 97%;
  background: rgba(212, 215, 217, 0.3);
  border-radius: 4px;
}
.m .surveyDetail .patient-pane .survey-detail-select .ant-select .ant-select-selection {
  height: 40px;
  border: 0;
  background: rgba(212, 215, 217, 0);
  margin-bottom: 10px;
}
.m .surveyDetail .patient-pane .survey-detail-select .ant-select-focused .ant-select-selection {
  box-shadow: none;
  border: 0;
}
.m .surveyDetail .patient-pane .survey-detail-select .ant-select-open .ant-select-selection {
  box-shadow: none;
  border: 0;
}
.m .surveyDetail .patient-pane .survey-detail-select .ant-select .ant-select-selection .ant-select-selection__rendered .ant-select-selection-selected-value {
  color: #2b3a42;
  font-family: "AvenirNext-Medium";
}
.m .surveyDetail .patient-pane .survey-detail-select .ant-select .ant-select-selection .ant-select-selection__rendered {
  margin: 0;
  padding: 5px 10px;
}
.m .surveyDetail .patient-pane .survey-detail-select .ant-select .ant-select-selection {
  height: 40px;
  display: flex;
  flex-grow: 1;
  justify-content: space-between;
  border: 0;
  background: rgba(212, 215, 217, 0);
  margin-bottom: 10px;
}
.m .surveyDetail .patient-pane .survey-detail-select .ant-select-selection__rendered {
  margin: 0;
  max-width: 100px;
}
.m .surveyDetail .patient-pane .range-picker .ant-calendar-picker {
  max-width: 250px;
}
.m .surveyDetail .patient-pane .patient-cards {
  max-width: 254px;
}
.m .surveyDetail .patient-pane .the-cards {
  height: auto;
  flex-wrap: wrap;
  padding: 24px 0 200px 0;
  align-content: flex-start;
}
.m .surveyDetail .survey-pane {
  height: 100%;
  margin: 0;
  overflow-y: auto;
}
.m .surveyDetail ::-webkit-scrollbar {
  width: 5px;
}
.m .surveyDetail ::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}
.m .surveyDetail ::-webkit-scrollbar-thumb {
  background: #7f888d;
  border-radius: 10px;
}
.m .surveyDetail ::-webkit-scrollbar-thumb:hover {
  background: #d4d7d9;
}
.m .surveyDetail .survey-input.ant-input-affix-wrapper .ant-input:not(:first-child) {
  padding-left: 40px;
  height: 40px;
}
.m .surveyDetail .sortBy {
  height: 40px;
  background: rgba(212, 215, 217, 0.3);
  width: 100%;
  border-radius: 4px;
}
.m .surveyDetail .sortBy .ant-select .ant-select-selection {
  height: 40px;
  flex-grow: 1;
  border: 0;
  background: rgba(212, 215, 217, 0);
  margin-bottom: 12px;
}
.m .surveyDetail .sortBy .ant-select-focused .ant-select-selection {
  box-shadow: none;
  border: 0;
}
.m .surveyDetail .sortBy .ant-select-open .ant-select-selection {
  box-shadow: none;
  border: 0;
}
.m .surveyDetail .sortBy .ant-select .ant-select-selection .ant-select-selection__rendered .ant-select-selection-selected-value {
  color: #2b3a42;
  font-family: "AvenirNext-Medium";
}
.m .surveyDetail .sortBy .ant-select .ant-select-selection .ant-select-selection__rendered {
  margin: 0;
  padding: 5px 10px;
}
.m .more_options {
  width: 50px;
}
#root .end-survey.ant-btn-background-ghost {
  border: solid 1px #ff530d !important;
  color: #ff530d !important;
}
.inprogress-participants {
  padding-bottom: 150px;
}
.inprogress-participants .patient.ant-card {
  width: 100%;
}
.inprogress-participants .patient.ant-card:hover {
  margin-right: 8px;
}
