.m .patient-survey-response-container-wrapper {
  display: flex;
  flex-direction: column;
  position: relative;
  top: 64px;
  height: calc(100vh - 64px);
  width: 100%;
  justify-content: space-between;
  background-color: rgba(212, 215, 217, 0.1);
}
.m .patient-survey-response-container-wrapper .patient-survey-response-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  overflow-y: auto;
  padding: 16px;
}
.m .patient-survey-response-container-wrapper .patient-survey-response-container .patient-img {
  width: 64px;
  height: 64px;
}
.m .patient-survey-response-container-wrapper .patient-survey-response-container .patient-img-container {
  width: 64px;
  height: 64px;
  background-color: #7f888d;
  border-radius: 50%;
}
.m .patient-survey-response-container-wrapper .patient-survey-response-container .patient-survey-response {
  width: 100%;
  padding: 24px 16px;
}
.m .patient-survey-response-container-wrapper .patient-survey-response-container .view-profile.ant-btn {
  width: 100%;
  height: 40px;
  border-radius: 4px;
  background-color: rgba(212, 215, 217, 0.3);
  margin-bottom: 16px;
  border: 0;
}
.m .patient-survey-response-container-wrapper .fixed-header-sticky-patient-response {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  height: 72px;
  padding: 16px;
  border: solid 1px #d4d7d9;
}
